import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Redirect, Link } from 'react-router-dom';

const navClasse = "nav p-2 bg-transparent d-flex flex-row justify-content-around menu-colunas";
const navCss = { top: '1.5%', position: 'absolute', width: '100%', zIndex: '0', margin: '0', padding: '0', whiteSpace:'nowrrap' };
const liClasse = "nav-item  text-center  rounded flex-fill ";
const liCss = { transition: '3s background-color ease-in', fontSize: '90%', fontWeight: 'bolder' }
const menuLinkClasse = "nav-link text-light ";
const menuLinkClasseUltima = "nav-link text-light ";
const linkCss = {
  color: 'white',
  fontWeight: 'bold'
}

class Menu extends Component {
  render() {

    return (

      <ul id="Menu-Nav" class={navClasse} style={navCss} >
      <div id='Menu1' style={{flexWrap:'nowrap',width:'50%',minWidth:'233px'}} class={navClasse}>
        <li class={liClasse} style={liCss}>
          <Link to='/apresentacao' class={menuLinkClasse} style={linkCss}>Apresentacão</Link>

        </li>
        <li class={liClasse} style={liCss}>
          <Link to="/objetivo" class={menuLinkClasse} style={linkCss}  >Objetivo</Link >
        </li>
        </div>
        <div id='Menu2' style={{flexWrap:'nowrap',width:'50%',minWidth:'233px'}} className={navClasse}>
        <li class={liClasse} style={liCss}>
          <Link to="/habilidades" class={menuLinkClasse} style={linkCss} >Habilidades</Link >
        </li>
        <li class={liClasse} style={liCss}>
          <Link to="/contato" class={menuLinkClasseUltima} style={linkCss} >Contatos</Link >
        </li>
        </div>
      </ul>


    );
  }
}
export default Menu;