import React, { Component } from 'react';
import logoReact from './imagens/logos/react.png';
import logoJquery from './imagens/logos/jquery.png';
import logoCsharp from './imagens/logos/csharp.png';
import logoNode from './imagens/logos/nodejs.png';


//Estilização 
const logosCss = {
    paddingTop:'10px',
    paddingBottom:'10px',
    bottom:'0',
    margin:'0',
    verticalAlign:'middle',
    height:'14%',
    position:'fixed',
    width:'100%'
    
}
const logosUlCss = {
    listStyle: 'none',
    margin:'0',
   

    
        
}
const liCss = {

    height:'100%',
    padding:'0',
    margin:'0'
   
}
const imgCss = {
    height:'55px',
    objectFit:'cover'
    

}
//Classe
class Logos extends Component {
    render() {

        return (
          
                <div id='logos' style={logosCss}>
                    <ul style={logosUlCss} className='d-flex flex-row justify-content-around'>
                        <li  style={liCss}><img style={imgCss}     src={logoReact} /></li>
                        <li style={liCss}><img style={imgCss}    src={logoJquery} /></li>
                        <li  style={liCss}><img style={imgCss}    src={logoNode} /></li>
                        <li    style={liCss} ><img style={imgCss}  src={logoCsharp} /></li>
                    </ul>

                </div>


            
        );
    }
}
export default Logos;